				orderInfo.sideItemPhoto = imageSide;
				// imageMalfunction
				var imageMalfunction = [];
				imageMalfunction.push(orderInfo.problemPhoto);
				orderInfo.problemPhoto = imageMalfunction;
				this.order = order;
			} */
