			signStatusHandle() {
				// this.isSigned = true
				this.order.customerSignature = this.$refs.orderConfirm.getImg()
				this.order.agreeRepair = true
			},
			confirmToAgreePricing() {
				var that = this
				that.$login.checkTokenValidity().then((login) => {
					if (!that.order.agreeRepair) {
						uni.showToast({
							title: '签名未上传完成，请稍等或重新签名！',
							icon: 'none'
						})
						return 
					}
					console.log('拿出来的：' + that.order.customerSignature)
					var orderId = that.order.id
					that.$api.apiPost({
						url: `/api/order/${orderId}/repair/confirm`,
						data: {
							customerSignature: that.order.customerSignature,
							isAgreeRepair: that.order.agreeRepair
						}
					})
					.then((res) => {
						uni.showToast({
							title: '已确认报价！',
							icon: 'none'
					})
					that.$websocket.sendData({
						orderId: that.order.id,
						orderStep: that.order.orderStep,
						notifyShopId:that.order.shopId,
						// notifyCustomerId: order.customerId
					})
						uni.navigateBack()
					})
					.catch((res) => {
						uni.showToast({
							title: '确认报价失败！',
							icon: 'none'
						})
					})
				})
			},
			confirmPayed() {
				var that = this
				
				that.$login.checkTokenValidity().then((login) => {
					uni.showModal({
						title: '是否已支付？',
						cancelText: '未支付',
						confirmText: '已支付',
						success(result) {
							if (result.confirm) {
								var orderId = that.order.id
								that.$api.apiPost({
									url: `/api/order/${orderId}/repair/doubleConfirm`
								})
								.then((res) => {
									uni.showToast({
										title: '已确认支付！',
										icon: 'none'
									})
									that.$websocket.sendData({
									orderId: that.order.id,
									orderStep: that.order.orderStep,
									// notifyShopId:that.order.shopId,
									notifyCustomerId: that.order.customerId
									})
									uni.navigateBack()
								})
								.catch((res) => {
									uni.showToast({
										title: '确认支付失败！',
										icon: 'none'
									})
								})
							}
						}
					})
					
				})
			},
			cancelOrder() {
				var that = this
				that.$login.checkTokenValidity().then((login) => {
					uni.showModal({
						title: '是否要取消订单？',
						cancelText: '不取消',
						confirmText: '确认取消',
						success(result) {
							if (result.confirm) {
								var orderId = that.order.id
								that.$api.apiPost({
									url: `/api/order/${orderId}/repair/cancel`
								})
								.then((res) => {
									uni.showToast({
										title: '已取消订单！',
										icon: 'none'
									})
									that.$websocket.sendData({
										orderId: that.order.id,
										orderStep: that.order.orderStep,
										notifyShopId:that.order.shopId,
										// notifyCustomerId: order.customerId
									})
									uni.navigateBack()
								})
								.catch((res) => {
									uni.showToast({
										title: '取消订单失败！',
										icon: 'none'
									})
								})
							}
						}
					})
					
				})
			}
		} // methods end
		
	}
